<template>
  <div
    class="trading-vue-legend"
    :style="calc_style"
  >
    <div
      v-if="grid_id === 0"
      class="trading-vue-ohlcv"
      :style="{ 'max-width': common.width + 'px' }"
    >
      <span
        class="t-vue-title"
        :style="{ color: common.colors.title }"
      >
        {{ common.title_txt }}
      </span>
      <span v-if="show_values">
        O<span class="t-vue-lspan">{{ ohlcv[0] }}</span> H<span
          class="t-vue-lspan"
        >{{ ohlcv[1] }}</span>
        L<span class="t-vue-lspan">{{ ohlcv[2] }}</span> C<span
          class="t-vue-lspan"
        >{{ ohlcv[3] }}</span>
        V<span class="t-vue-lspan">{{ ohlcv[4] }}</span>
      </span>
      <span
        v-if="!show_values"
        class="t-vue-lspan"
        :style="{ color: common.colors.text }"
      >
        {{ (common.meta.last || [])[4] }}
      </span>
    </div>
    <div
      v-for="(ind, i) in indicators"
      :key="'indicator-toolbar' + i"
      class="t-vue-ind"
    >
      <span class="t-vue-iname">{{ ind.name }}</span>
      <button-group
        :buttons="common.buttons"
        :config="common.config"
        :ov_id="ind.id"
        :grid_id="grid_id"
        :index="ind.index"
        :tv_id="common.tv_id"
        :display="ind.v"
        @legend-button-click="button_click"
      />
      <span
        v-if="ind.v && show_values"
        class="t-vue-ivalues"
      >
        <span
          v-for="(v, iv) in ind.values"
          :key="'legend-span' + iv"
          class="t-vue-lspan t-vue-ivalue"
          :style="{ color: v.color }"
        >
          {{ v.value }}
        </span>
      </span>
      <span
        v-if="ind.unk"
        class="t-vue-unknown"
      > (Unknown type) </span>
      <transition name="tvjs-appear">
        <spinner
          v-if="ind.loading"
          :colors="common.colors"
        />
      </transition>
    </div>
  </div>
</template>
<script>
import ButtonGroup from "./ButtonGroup.vue";
import Spinner from "./Spinner.vue";

export default {
  name: "ChartLegend",
  components: { ButtonGroup, Spinner },
  props: ["common", "values", "grid_id", "meta_props"],
  computed: {
    ohlcv() {
      if (!this.values || !this.values.ohlcv) {
        return Array(6).fill("n/a");
      }
      const prec = this.layout.prec;

      // TODO: main the main legend more customizable
      let id = this.main_type + "_0";
      let meta = this.meta_props[id] || {};
      if (meta.legend) {
        return (meta.legend() || []).map((x) => x.value);
      }

      return [
        this.values.ohlcv[1].toFixed(prec),
        this.values.ohlcv[2].toFixed(prec),
        this.values.ohlcv[3].toFixed(prec),
        this.values.ohlcv[4].toFixed(prec),
        this.values.ohlcv[5] ? this.values.ohlcv[5].toFixed(2) : "n/a",
      ];
    },
    // TODO: add support for { grid: { id : N }}
    indicators() {
      const values = this.values;
      const f = this.format;
      let types = {};

      return this.json_data
        .filter((x) => x.settings.legend !== false && !x.main)
        .map((x) => {
          if (!(x.type in types)) types[x.type] = 0;
          const id = x.type + `_${types[x.type]++}`;
          return {
            v: "display" in x.settings ? x.settings.display : true,
            name: x.name || id,
            index: (this.off_data || this.json_data).indexOf(x),
            id: id,
            values: values ? f(id, values, x.settings?.labels) : this.n_a(1),
            unk: !(id in (this.meta_props || {})),
            loading: x.loading,
          };
        });
    },
    calc_style() {
      let top = this.layout.height > 150 ? 10 : 5;
      let grids = this.common.layout.grids;
      let w = grids[0] ? grids[0].width : undefined;
      return {
        top: `${this.layout.offset + top}px`,
        width: `${w - 20}px`,
      };
    },
    layout() {
      const id = this.grid_id;
      return this.common.layout.grids[id];
    },
    json_data() {
      return this.common.data;
    },
    off_data() {
      return this.common.offchart;
    },
    main_type() {
      let f = this.common.data.find((x) => x.main);
      return f ? f.type : undefined;
    },
    show_values() {
      return this.common.cursor.mode !== "explore";
    },
  },
  methods: {
    format(id, values, labels) {
      let meta = this.meta_props[id] || {};
      // Matches Overlay.data_colors with the data values
      // (see Spline.vue)
      if (!values[id]) return this.n_a(1);

      // Custom formatter
      if (meta.legend) return meta.legend(values[id]);

      return values[id].slice(1).map((x, i) => {
        const cs = meta.data_colors ? meta.data_colors() : [];
        if (typeof x == "number") {
          // Show 8 digits for small values
          x = x.toFixed(Math.abs(x) > 0.001 ? 4 : 8);
        }
        if (labels) {
          x = labels[i % labels.length] + ": " + x;
        }

        return {
          value: x,
          color: cs ? cs[i % cs.length] : undefined,
        };
      });
    },
    n_a(len) {
      return Array(len).fill({ value: "n/a" });
    },
    button_click(event) {
      this.$emit("legend-button-click", event);
    },
  },
};
</script>
<style>
.trading-vue-legend {
  position: relative;
  z-index: 100;
  font-size: 1.25em;
  margin-left: 10px;
  pointer-events: none;
  text-align: left;
  user-select: none;
  font-weight: 300;
}
@media (min-resolution: 2x) {
  .trading-vue-legend {
    font-weight: 400;
  }
}
.trading-vue-ohlcv {
  pointer-events: none;
  margin-bottom: 0.5em;
}
.t-vue-lspan {
  font-variant-numeric: tabular-nums;
  font-size: 0.95em;
  color: #999999; /* TODO: move => params */
  margin-left: 0.1em;
  margin-right: 0.2em;
}
.t-vue-title {
  margin-right: 0.25em;
  font-size: 1.45em;
}
.t-vue-ind {
  margin-left: 0.2em;
  margin-bottom: 0.5em;
  font-size: 1em;
  margin-top: 0.3em;
}
.t-vue-ivalue {
  margin-left: 0.5em;
}
.t-vue-unknown {
  color: #999999; /* TODO: move => params */
}

.tvjs-appear-enter-active,
.tvjs-appear-leave-active {
  transition: all 0.25s ease;
}

.tvjs-appear-enter,
.tvjs-appear-leave-to {
  opacity: 0;
}
</style>
